@media only screen and (max-width: 480px) {
    .page-profile {
        background-color: white;
        display: flex;
        width: 100vw;
        height: 100vh;
    }
    
    .profile-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
    }
    
    .profile {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 70%;
        height: auto;
        box-sizing: border-box;
    }
    
    .profile-data {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 60%;
        box-sizing: border-box;
    }
    
    .profile-data > h5 {
        font-size: 15px;
        font-family: sans-serif;
        margin: 5px;
    }
    
    .profile-data > p {
        font-family: sans-serif;
        font-size: 18px;
        margin: 5px;
        color: black;
    }
    
    .profile-image {
        text-align: right;
        justify-content: center;
        height: 100%;
    }
    
    .profile-image > img {
        width: 200px;
        height: auto;
        border-radius: 50%;
    }
    
    .div-button {
        margin-top: 15px;
        width: 100%;
        text-align: center;
    }
    
    .button-edit {
        padding: 10px;
        border: none;
        border-radius: 20px;
        font-family: sans-serif;
        background-color: orangered;
        color: white;
    }
    
    .page-profile-edit {
        background-color: white;
        display: flex;
        width: 100vw;
        height: 100vh;
    }
    
    .profile-edit {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    
    .profile-inputs {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
    }
    
    .profile-inputs input {
        font-family: sans-serif;
        padding: 10px;
        width: 70%;
        margin-top: 15px;
        height: 35px;
        border: none;
        border-radius: 20px;
        box-shadow: 1px 1px 10px orangered;
    }
    
    .profile-inputs textarea {
        padding: 10px;
        font-family: sans-serif;
        width: 70%;
        margin-top: 15px;
        height: 150px;
        border: none;
        border-radius: 20px;
        box-shadow: 1px 1px 20px orangered;
    }
    
    .profile-inputs button {
        margin-top: 15px;
        border: none;
        border-radius: 20px;
        height: 35px;
        width: 150px;
        box-shadow: 1px 1px 20px orangered;
        font-family: sans-serif;
    }

}

